<script>
export default {
  name: 'GameView',
  data(){
    return{
      step : 1,
      tutor : true,
      select_box :false,
      collect_box : false,
      error_box : false,
      orgin_img_list : [
        {
          num : 1,
          corrected : false,
          img : require('@/assets/second/img01.png'),
          num_img : require('@/assets/correct/01.png'),
        },
        {
          num : 2,
          corrected : false,
          img : require('@/assets/second/img02.png'),
          num_img : require('@/assets/correct/02.png'),
        },
        {
          num : 3,
          corrected : false,
          img : require('@/assets/second/img03.png'),
          num_img : require('@/assets/correct/03.png'),
        },
        {
          num : 4,
          corrected : false,
          img : require('@/assets/second/img04.png'),
          num_img : require('@/assets/correct/04.png'),
        },
        {
          num : 5,
          corrected : false,
          img : require('@/assets/second/img05.png'),
          num_img : require('@/assets/correct/05.png'),
        },
        {
          num : 6,
          corrected : false,
          img : require('@/assets/second/img06.png'),
          num_img : require('@/assets/correct/06.png'),
        },
      ],
      select_img_list : [
        {
          num : 1,
          img : require('@/assets/second/img01.png'),
          num_img : require('@/assets/correct/01.png'),
        },
        {
          num : 2,
          img : require('@/assets/second/img02.png'),
          num_img : require('@/assets/correct/02.png'),
        },
        {
          num : 3,
          img : require('@/assets/second/img03.png'),
          num_img : require('@/assets/correct/03.png'),
        },
        {
          num : 4,
          img : require('@/assets/second/img04.png'),
          num_img : require('@/assets/correct/04.png'),
        },
        {
          num : 5,
          img : require('@/assets/second/img05.png'),
          num_img : require('@/assets/correct/05.png'),
        },
        {
          num : 6,
          img : require('@/assets/second/img06.png'),
          num_img : require('@/assets/correct/06.png'),
        },
      ],
      select_num : 0,
      videoSelectPopup : false,
    }
  },

  methods:{
    onTutorOpen(){
      this.tutor = false
    },
    onNextStep(){
      this.step = this.step+1;
      if(this.step == 2){
        this.reset();
        this.shuffleArray();
      }
    },
    onPreveStep(){
      this.reset();
      this.step = this.step-1;
    },
    onReset(){
      this.reset();
      this.step=1;
    },
    onHandleSelectPopup(num){
      let already = false;
      for(let item of this.select_img_list){
        if(item.num === num && item.corrected){
          already = true
          break;
        }
      }
      if(!already){
        this.select_num = num;
        this.select_box = !this.select_box;
      }
    },
    onHandleCorrectPopup(){
      this.collect_box = !this.collect_box;
    },
    onHandleErrorPopup(){
      this.error_box = !this.error_box;
    },
    onHandleChoiceNumber(num){
      let select_img_list = this.orgin_img_list;
      let fail = true;
      let already= false;
      if(this.select_num == num){
        for(let key in select_img_list){
          if(select_img_list[key].num === this.select_num){
            this.onHandleCorrectPopup();
            this.onHandleSelectPopup(0);
            fail = false
            select_img_list[key].corrected = true;
            break;
          }
        }
      }
  
      for(let key in select_img_list){
        if(select_img_list[key]['num'] == num && select_img_list[key]['corrected']){
          already = true;
          break;
        }
      }

      if(fail && !already){
        this.onHandleErrorPopup();
      }

      this.orgin_img_list = select_img_list;

      let cnt = 0;
      for(let item of select_img_list){
        if(item.corrected){
          cnt++;
        }
      }
      if(cnt == select_img_list.length){
        setTimeout(() => {
          this.onNextStep();
        }, 1000);
      }

    },
    reset(){
      for(let item of this.orgin_img_list){
        item.corrected = false;
      }
    },
    shuffleArray(){
      this.select_img_list = [...this.orgin_img_list]; // 원본 배열 복사
      for (let i = this.select_img_list.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [this.select_img_list[i], this.select_img_list[j]] = [this.select_img_list[j], this.select_img_list[i]];
      }
    },
    onOpenVideoSelect(){
      this.videoSelectPopup = !this.videoSelectPopup;
    }
  }
}
</script>
